.boards {
  max-width: rem(1030px);
  max-height: rem(600px);
  width: 90%;
  height: 55%;
  
  background-image: linear-gradient(-225deg, $lightBlue 27%, #ffffff 27%);
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.5);
  display: flex;

  position: relative;
}

.boards__left {
  width: 52%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: rem(30px);

  border: 1px solid yellow;
}

.boards__right {
  width: 48%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: rem(45px) rem(30px);

  border: 1px solid yellow;
}

// left part of boards

.boards__image {
  max-width: rem(325px);
  max-height: rem(450px);
  width: 90%;
  margin-right: rem(30px);
}


.boards__gallery {
  display: flex;
  flex-direction: column;
}

.boards__item {
  margin-bottom: rem(5px);

  &:last-child {
    margin-bottom: rem(0px);
  }
}

.boards__thumbnail {
  width: rem(70px);
  height: rem(70px);
  cursor: pointer;
  border: 3px solid white;
  overflow: hidden;

  &:hover {
    border: 3px solid $blue;
  }
}

// right part of boards

.boards__name {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: rem(20px);
  line-height: rem(26px);
}

.boards__rating {
  display: flex;
  align-items: center;
}

.boards__star {
  width: rem(14px);
  height: rem(14px);
  stroke: $gray;
  fill: #fff;

  &--yellow {
    stroke: $yellow;
    fill: $yellow;
  }
}

.boards__voices {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color: $gray;
}

.boards__topics {
  display: flex;
  border-bottom: 1px solid $lightGray;
  padding-bottom: rem(5px);
}

.boards__topic {
  margin-right: rem(30px);

  &:last-child {
    margin-right: rem(0px);
  }

  &:hover {
    
  }
}

.boards__topic-link {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  color: $lightGray;
  text-transform: uppercase;
}

.boards__text {
  font-family: 'Raleway', sans-serif;
  line-height: rem(24px);
  color: $gray;
}

.boards__bottom {
  display: flex;
  align-items: baseline;
}

.boards__price {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: rem(24px);
  margin-right: rem(30px);
}



// images

.boargs__bigpic, .boards__smallpic {
  width: 100%;
  height: 100%;
}
